/* custom css for results component */

a {
  color: #1A1F16;
}

a:hover {
  text-decoration: none;
  color: #fff;
  -webkit-transition: all, 0.3s;
  -o-transition: all, 0.3s;
  transition: all, 0.3s;
}

hr {
  width: 50%;
  background-color: #fff;
}


